import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import { Link } from "react-router-dom";

import notification from "../../../../components/notification";
import { Empty, Input, Collapse, Select, Tooltip } from "antd";

import Popconfirms from "../../../../components/feedback/popconfirm";
// import Box from '../../components/utility/box';
// import HelperText from "../../../../components/utility/helper-text";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/payment/paymentCategory/actions";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper, {
  Box,
  StatusTag,
  customCollapseStyle
} from "../../../App/app.style";
import getLanguage from "../../../App/MyLanguange";

const Panel = Collapse.Panel;
const Option = Select.Option;
const Search = Input.Search;
const redirectTo = "/dashboard/setup/menus";

class PaymentCategory extends Component {
  state = {
    filter: {
      id_account: [],
      pagination: null
    },
    search: null
  };

  // Get All data diawal
  componentDidMount() {
    const { getPaymentCategories } = this.props;
    getPaymentCategories();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.paymentCategory.error) {
      notification("error", this.props.paymentCategory.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.paymentCategory.success) {
      notification("success", this.props.paymentCategory.success);
      this.props.clearSuccess();
    }
  }
  // Handle search
  handleSearch = value => {
    console.log(value);
    const { getPaymentCategories } = this.props;
    this.setState({
      filter: {
        id_account: [],
        pagination: null
      }
    });
    getPaymentCategories(1, value);
  };

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;

    this.setState({ search: value });
  };

  // Handle Reset search
  handleResetSearch = () => {
    const { getPaymentCategories } = this.props;
    this.setState({
      filter: {
        id_account: [],
        pagination: null
      }
    });
    this.setState({ search: null });
    getPaymentCategories();
  };

  // Handle filter change
  handleFilterChange = (name, value) => {
    this.setState({
      filter: {
        ...this.state.filter,
        [name]: value
      }
    });
  };

  // Handle filter
  handleFilter = () => {
    const { getPaymentCategories } = this.props;
    //const { current_page } = this.props.paymentCategory;
    const { filter } = this.state;
    let string = "";

    if (filter.id_account) {
      string += "id_account=" + filter.id_account;
    }
    if (filter.pagination) {
      string
        ? (string += "&pagination=" + filter.pagination)
        : (string += "pagination=" + filter.pagination);
    }

    this.setState({ search: null });
    getPaymentCategories(1, null, string);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    const { getPaymentCategories } = this.props;
    this.setState({
      filter: {
        id_account: [],
        pagination: null
      }
    });
    this.setState({ search: null });
    getPaymentCategories();
  };

  // Handle action di table
  handleRecord = (action, id) => {
    if (action === "activate") {
      this.props.activatePaymentCategory(id);
    } else {
      this.props.deactivatePaymentCategory(id);
    }
  };

  // Handle pagination
  handlePageChange = page => {
    const { getPaymentCategories } = this.props;
    const { search } = this.props.paymentCategory;
    const { filter } = this.props.paymentCategory;
    getPaymentCategories(page, search, filter);
  };

  listAction = {
    title: "",
    dataIndex: "id_payment_category",
    rowKey: "id_payment_category",
    width: "10%",
    align: "center",
    fixed: "right",
    render: (text, row) => (
      <div>
        <Tooltip
          placement="topLeft"
          title={<IntlMessages id="tooltip.update" />}
        >
          <Link to={`${this.props.match.path}/update/${text}`}>
            <Button size="small" className="invoiceEdtBtn">
              <i className="ion-android-create" />
            </Button>
          </Link>
        </Tooltip>

        {row.is_active ? (
          <Popconfirms
            title={
              <IntlMessages id="paymentCategories.popconfirm.deactivate" />
            }
            onConfirm={this.handleRecord.bind(this, "deactivate", text)}
            okText={getLanguage({ id: "yes" })}
            cancelText={getLanguage({ id: "no" })}
          >
            <Tooltip
              placement="topRight"
              title={<IntlMessages id="tooltip.deactivate" />}
            >
              <Button size="small" className="invoiceDltBtn">
                <i className="ion-close-circled" />
              </Button>
            </Tooltip>
          </Popconfirms>
        ) : (
          <Popconfirms
            title={<IntlMessages id="paymentCategories.popconfirm.activate" />}
            onConfirm={this.handleRecord.bind(this, "activate", text)}
            okText={getLanguage({ id: "yes" })}
            cancelText={getLanguage({ id: "no" })}
          >
            <Tooltip
              placement="topRight"
              title={<IntlMessages id="tooltip.activate" />}
            >
              <Button size="small" className="invoiceRstBtn">
                <i className="ion-refresh" />
              </Button>
            </Tooltip>
          </Popconfirms>
        )}
      </div>
    )
  };

  render() {
    // console.log(this.state.filter);
    const // Definisi kolom2 serta field2 API yang dipakai tabel
      columns = [
        {
          title: getLanguage({ id: "journals.add.code" }),
          dataIndex: "payment_category_code",
          rowKey: "payment_category_code",
          width: "10%",
          render: text => <span>{text}</span>
        },
        {
          title: getLanguage({ id: "journals.add.name" }),
          dataIndex: "description",
          rowKey: "description",
          width: "15%",
          render: display => {
            return display.length > 50
              ? display.substring(0, 50) + "..."
              : display;
          }
        },
        {
          title: getLanguage({ id: "sidebar.account" }),
          dataIndex: "account",
          rowKey: "account",
          width: "10%",
          render: (text, row) => {
            if (text) {
              return (
                <span>
                  {text.account_code + " (" + text.account_name + ")"}
                </span>
              );
            } else return "-";
          }
        },
        {
          title: getLanguage({ id: "sidebar.location" }),
          dataIndex: "location",
          rowKey: "location",
          width: "10%",
          render: (text, row) => {
            if (text) {
              return <span>{text.location_name}</span>;
            } else return "-";
          }
        },
        {
          title: getLanguage({ id: "sidebar.cashBankReport" }),
          dataIndex: "cash_bank",
          rowKey: "cash_bank",
          width: "10%",
          render: (text, row) => {
            if (text) {
              return (
                <span>
                  {text.cash_bank_code + " (" + text.description + ")"}
                </span>
              );
            } else return "-";
          }
        },
        {
          title: getLanguage({ id: "paymentType" }),
          dataIndex: "payment_type",
          rowKey: "payment_type",
          width: "10%",
          align: "center",
          render: text => {
            let className;
            let display;
            if (text === 1) {
              className = "primary";
              display = getLanguage({ id: "cash" });
            } else if (text === 2) {
              className = "ordered";
              display = getLanguage({ id: "credit" });
            } else if (text === 3) {
              className = "closed";
              display = getLanguage({ id: "card" });
            } else {
              className = "cancelled";
              display = getLanguage({ id: "other" });
            }
            return <StatusTag className={className}>{display}</StatusTag>;
          }
        },
        {
          title: getLanguage({ id: "isDefault" }),
          dataIndex: "is_default",
          rowKey: "is_default",
          width: "5%",
          align: "center",
          render: text => {
            let className;
            let display;
            if (text === 1) {
              className = "primary";
              display = <IntlMessages id="yes" />;
            } else {
              className = "nonactive";
              display = <IntlMessages id="no" />;
            }
            return <StatusTag className={className}>{display}</StatusTag>;
          }
        },
        {
          title: getLanguage({ id: "status" }),
          dataIndex: "is_active",
          rowKey: "is_active",
          width: "10%",
          align: "center",
          render: text => {
            let className;
            let display;
            if (text === 1) {
              className = "active";
              display = getLanguage({ id: "active" });
            } else {
              className = "nonactive";
              display = getLanguage({ id: "inactive" });
            }
            return <StatusTag className={className}>{display}</StatusTag>;
          }
        }
      ];

    // Data setup
    const { payment_categories } = this.props.paymentCategory;
    //console.log(this.props.paymentCategory); //console.log(this.state);
    const dataSource = [];
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    //console.log(permissions);
    if (
      permissions.includes("Update Payment Category") &&
      columns.length === 8
    ) {
      columns.push(this.listAction);
    }

    Object.keys(payment_categories).map((paymentCategory, index) => {
      return dataSource.push({
        ...payment_categories[paymentCategory],
        key: paymentCategory
      });
    });

    // Pagination Setup
    const { current_page, page_size, total } = this.props.paymentCategory;

    //console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="paymentCategories.pageHeader" />}
            marginBottom={true}
          >
            <div style={{ display: "flex", justifyContent: "flex-end" }}>
              {permissions.includes("Create Payment Category") ? (
                <Link to={`${this.props.match.path}/add`}>
                  <Button type="primary" className="mateAddInvoiceBtn">
                    <IntlMessages id="paymentCategories.button.add" />
                  </Button>
                </Link>
              ) : null}
              <Button
                onClick={() => this.props.history.replace(redirectTo)}
                style={{ marginLeft: "5px" }}
              >
                <IntlMessages id="button.back" />
              </Button>
            </div>
          </PageHeader>
          <div className="isoInvoiceTableBtn">
            <Search
              placeholder={
                getLanguage({ id: "journals.add.code" }) +
                ", " +
                getLanguage({ id: "journals.add.name" })
              }
              onSearch={this.handleSearch}
              style={{ marginRight: "1%" }}
              value={this.state.search}
              onChange={this.handleSearchValue}
            />
            <div
              style={{
                // width: "20%",
                marginTop: "auto",
                marginRight: "1%"
              }}
            >
              <Button
                type="primary"
                onClick={this.handleSearch.bind(this, this.state.search)}
              >
                <IntlMessages id="button.search" />
              </Button>
            </div>

            <div
              style={{
                width: "30%",
                marginTop: "auto",
                marginRight: "27%"
                // marginLeft: "-2%"
              }}
            >
              <Button onClick={this.handleResetSearch}>
                <IntlMessages id="filter.reset" />
              </Button>
            </div>
          </div>

          <div className="isoFilter">
            <Collapse style={customCollapseStyle}>
              <Panel header={<IntlMessages id="filter.header" />} key="1">
                <div className="isoFilterContent">
                  <div style={{ width: "50%" }}>
                    <label>
                      <IntlMessages id="directSalesAccount" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_account"
                      )}
                      value={
                        this.state.filter.id_account
                          ? this.state.filter.id_account
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={
                        <IntlMessages id="adjustmentTypes.filter.select.account" />
                      }
                      loading={this.props.paymentCategory.loading}
                      allowClear={true}
                    >
                      {this.props.paymentCategory.accounts.map((data, key) => (
                        <Option key={key} value={data.id_account}>
                          {data.account_code + " (" + data.account_name + ")"}
                        </Option>
                      ))}
                    </Select>
                  </div>

                  <div style={{ width: "15%" }}>
                    <label>
                      <IntlMessages id="dataPerPage" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "pagination"
                      )}
                      value={
                        this.state.filter.pagination
                          ? this.state.filter.pagination
                          : page_size
                      }
                      style={{ width: "95%" }}
                      optionFilterProp="children"
                      filterOption={true}
                      loading={this.props.paymentCategory.loading}
                      // allowClear={true}
                      // showArrow={false}
                    >
                      <Option key={1} value={10}>
                        10
                      </Option>
                      <Option key={2} value={25}>
                        25
                      </Option>
                      <Option key={3} value={50}>
                        50
                      </Option>
                      <Option key={4} value={100}>
                        100
                      </Option>
                    </Select>
                  </div>

                  <div
                    style={{
                      width: "20%",
                      marginTop: "auto"
                    }}
                  >
                    <Button
                      size="small"
                      type="primary"
                      style={{ marginRight: "10px" }}
                      onClick={this.handleFilter}
                    >
                      <IntlMessages id="filter.submit" />
                    </Button>
                    <Button onClick={this.handleResetFilter} size="small">
                      <IntlMessages id="filter.reset" />
                    </Button>
                  </div>
                </div>
              </Panel>
            </Collapse>
          </div>

          <CardWrapper>
            {payment_categories.length === 0 &&
            !this.props.paymentCategory.loading ? (
              <Empty
                description={
                  <IntlMessages id="paymentCategories.table.noData" />
                }
              />
            ) : (
              <div className="isoInvoiceTable">
                <Scrollbars style={{ width: "100%" }}>
                  <TableWrapper
                    dataSource={dataSource}
                    columns={columns}
                    loading={this.props.paymentCategory.loading}
                    className="invoiceListTable"
                    bordered={true}
                    // Set pagination, jika ada page_size mengunakan pagination API
                    // jika tidak ada page_size menggunakan pagination bawaan
                    pagination={
                      page_size
                        ? {
                            current: current_page,
                            pageSize: parseInt(page_size),
                            total: total,
                            onChange: this.handlePageChange.bind(this)
                          }
                        : true
                    }
                  />
                </Scrollbars>
              </div>
            )}
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    paymentCategory: state.paymentCategory,
    auth: state.auth
  };
};

export default compose(connect(mapStateToProps, actions))(PaymentCategory);
