const actions = {
  getPaymentCategories: (page, search, filter) => ({
    type: 'GET_PAYMENT_CATEGORIES_REQUEST',
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  createPaymentCategory: () => ({
    type: 'CREATE_PAYMENT_CATEGORY_REQUEST'
  }),
  storePaymentCategory: data => ({
    type: 'STORE_PAYMENT_CATEGORY_REQUEST',
    payload: { body: data }
  }),
  editPaymentCategory: id_payment_category => ({
    type: 'EDIT_PAYMENT_CATEGORY_REQUEST',
    payload: { id_payment_category: id_payment_category }
  }),
  updatePaymentCategory: (id_payment_category, data) => ({
    type: 'UPDATE_PAYMENT_CATEGORY_REQUEST',
    payload: {
      id_payment_category: id_payment_category,
      body: data
    }
  }),
  showPaymentCategory: id_payment_category => ({
    type: 'SHOW_PAYMENT_CATEGORY_REQUEST',
    payload: { id_payment_category: id_payment_category }
  }),
  deactivatePaymentCategory: id_payment_category => ({
    type: 'DEACTIVATE_PAYMENT_CATEGORY_REQUEST',
    payload: { id_payment_category }
  }),
  activatePaymentCategory: id_payment_category => ({
    type: 'ACTIVATE_PAYMENT_CATEGORY_REQUEST',
    payload: { id_payment_category }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
